import React from "react";
import { Box, Icon } from 'react-bulma-components';
// import Icon from 'react-bulma-components/lib/components/icon';
// import Box from 'react-bulma-components/lib/components/box';


class WinnerArrow extends React.Component {





  render() {

    const leftDivisions = ['a', 'b']
    const rightDivisions = ['c', 'd']

    return (
      <Box style={{ padding: '.25rem',  position: 'relative', 'boxShadow': 'none'}}>

        {((leftDivisions.includes(this.props.division) && this.props.matchId % 2 === 0) && (leftDivisions.includes(this.props.division) && !this.props.arrowOverride)) &&
            <Icon style={{left: 0,
                top: '3rem',
                position: 'absolute',}}>
                <i className={"fa fa-level-down-alt is-size-1"}></i>
            </Icon>
        }
        {((leftDivisions.includes(this.props.division) && this.props.matchId % 2 === 1) || (leftDivisions.includes(this.props.division) && this.props.arrowOverride)) &&
            <Icon style={{left: 0,
                top: '1.5rem',
                position: 'absolute',}}>
                <i className={"fa fa-level-up-alt is-size-1"}></i>

            </Icon>
        }
        {/* <span>{this.props.matchId + " " + this.props.division + " " + this.props.arrowOverride}</span> */}


        {((rightDivisions.includes(this.props.division) && this.props.matchId % 2 === 0) && (rightDivisions.includes(this.props.division) && !this.props.arrowOverride)) &&
            <Icon style={{right: 0,
                top: '3rem',
                position: 'absolute',}}>
                <i className={"fa fa-level-up-alt fa-flip-horizontal fa-flip-vertical is-size-1"}></i>
            </Icon>
        }
        {((rightDivisions.includes(this.props.division) && this.props.matchId % 2 === 1) || (rightDivisions.includes(this.props.division) && this.props.arrowOverride)) &&
            <Icon style={{right: 0,
                top: '1.5rem',
                position: 'absolute',}}>
                <i className={"fa fa-level-down-alt fa-flip-horizontal fa-flip-vertical is-size-1"}></i>
            </Icon>
        }
      </Box>
    );
  }
}

export default WinnerArrow;