import React from "react";
import { Columns } from 'react-bulma-components';
// import Columns from 'react-bulma-components/lib/components/columns';
import Matchup from "./Matchup";
import WinnerArrow from "./WinnerArrow"

class BracketRow extends React.Component {
    render() {
      let showWinnerArrow = true;
      return (
          <div>
            <Columns>

            {this.props.rounds[0].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.round === 0 &&
                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                }
                {!this.props.rounds[1].show &&
                    <Columns.Column size={1}  style={{ padding: '.25rem' }}>
                    {this.props.round === 5 &&
                        <span> </span>
                    }
                    </Columns.Column>
                }
                </Columns.Column>
            }
            {this.props.rounds[1].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[0].show && this.props.round === 0  && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[0]}
                    matchId={this.props.matchId}
                  ></WinnerArrow>
                }

                {this.props.round === 1 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                {!this.props.rounds[2].show &&
                    <Columns.Column size={1}  style={{ padding: '.25rem' }}>
                    {this.props.round === 5 &&
                        <span> </span>
                    }
                    </Columns.Column>
                }
                </Columns.Column>
            }
            {this.props.rounds[2].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[1].show && this.props.round === 1  && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[0]}
                    matchId={this.props.matchId}
                  ></WinnerArrow>
                }
                {this.props.round === 2 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                {!this.props.rounds[3].show &&
                    <Columns.Column size={1}  style={{ padding: '.25rem' }}>
                    {this.props.round === 5 &&
                        <span> </span>
                    }
                    </Columns.Column>
                }
                </Columns.Column>
            }
            {this.props.rounds[3].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[2].show && this.props.round === 2  && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[0]}
                    matchId={this.props.matchId}
                  ></WinnerArrow>
                }
                {this.props.round === 3 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                {!this.props.rounds[4].show &&
                    <Columns.Column size={1}  style={{ padding: '.25rem' }}>
                    {this.props.round === 5 &&
                        <span> </span>
                    }
                    </Columns.Column>
                }
                </Columns.Column>
            }
            {this.props.rounds[4].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[3].show && this.props.round === 3  && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[0]}
                    matchId={this.props.matchId}
                    arrowOverride={this.props.arrowOverride}
                  ></WinnerArrow>
                }
                {this.props.round === 4 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }

                {!this.props.rounds[5].show &&
                    <Columns.Column size={1}  style={{ padding: '.25rem' }}>
                    {this.props.round === 5 &&
                        <span> </span>
                    }
                    </Columns.Column>
                }
                </Columns.Column>
            }
            {this.props.rounds[5].show &&
                <Columns.Column size={this.props.championshipWidth}  style={{ padding: '.25rem' }}>
                {this.props.round === 5 &&
                    <Matchup
                    pickWinner={this.props.pickWinner}
                    unPickWinner={this.props.unPickWinner}
                    round={this.props.round}
                    division={this.props.divisions[2]}
                    matchId={this.props.matchId}
                    key={this.props.matchId}
                    teams={this.props.teams}
                    bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
            }
            {this.props.rounds[4].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[3].show && this.props.round === 3 && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[1]}
                    matchId={this.props.matchId}
                    arrowOverride={this.props.arrowOverride}
                  ></WinnerArrow>
                }
                {this.props.round === 4 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
            }
            {this.props.rounds[3].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[2].show && this.props.round === 2 && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[1]}
                    matchId={this.props.matchId}
                    arrowOverride={this.props.arrowOverride}
                  ></WinnerArrow>
                }
                {this.props.round === 3 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
            }
            {this.props.rounds[2].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[1].show && this.props.round === 1 && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[1]}
                    matchId={this.props.matchId}
                  ></WinnerArrow>
                }
                {this.props.round === 2 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>

            }
            {this.props.rounds[1].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.rounds[0].show && this.props.round === 0 && showWinnerArrow &&
                  <WinnerArrow
                    division={this.props.divisions[1]}
                    matchId={this.props.matchId}
                  ></WinnerArrow>
                }
                {this.props.round === 1  &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
            }
            {this.props.rounds[0].show &&
                <Columns.Column size={this.props.earlyRoundWidth}  style={{ padding: '.25rem' }}>
                {this.props.round === 0 &&
                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={0}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                }
                </Columns.Column>
            }
            </Columns>
          </div>
        );
    }
}


export default BracketRow;